@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&family=Roboto:wght@300&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root{
  --primary-white:#fff;
  --primay-maindark:#181616;
}
body{
  background-color: #22254b;
  color: #fff;
  color: var(--primary-white);
  font-family: "poppins", sans-serif;
  font-family: 'Roboto', sans-serif;
  margin: 0;
}
/***************** Movies *********************/
.movie-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.movie{
  position: relative;
  width: 300px;
  margin: 1rem;
  border-radius: 3px;
  box-shadow: 3px 3px 5px   rgba(0, 0, 0, 0.1);
  overflow: hidden;
}
.movie img{
  object-fit: cover;
  height: 400px;
  width: 100%;
}

.movie-info{
 display: flex;
 align-items: center;
 padding: 1rem;
 justify-content: space-between;
}

.movie-info h3{
  font-size: 1.3rem;
  margin: 0;
}

.movie-over{
  /* background-color: var(--primary-white); */
  background-color:#181616;
  background-color:var(--primay-maindark);
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 1rem;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.movie-over h2{
  color: #fff;
  color: var(--primary-white);
}

.movie:hover .movie-over{
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

 
.myform{
  display: flex;
  justify-content: space-between;
  padding: 0.3rem;
  margin-left: auto;

}
.logo{
  width: 50px;
  margin-right: 1rem;
}

/**************** Header *****************/
.gfg{
  position: relative;
}
.header-text{
  position: absolute;
  bottom: 0;
  padding: 20px;
  color: #fff;
  color: var(--primary-white);
}
.header-text h1{
  font-size: 7vw;

}
.header-text h3{
  font-size: 2vw;

}

/*********** Footer ***************/
.main-footer {
  background-color: #181616;
  background-color: var(--primay-maindark);
  padding: 2rem;
}
.main-footer  li,h4,p,h6{
  color: #fff;
  color: var(--primary-white);
}
.logo-footer{
  width: 200px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


